<template>
    <div class="header">
        <div class="logo" @click="goTo('home')">
            <h4 class="sweet">Sweet Dish</h4>
            <img src="../../assets/breakfast.jpg" style="margin-left:10px;width:45px;height:45px;">
        </div>
        <div class="d-flex cart">
            <i class="fa fa-shopping-cart cart-icon" @click="goTo('cart')"></i>
            <span class="cart-count">{{count}}</span>
            <h5 class="login">Login</h5>
            <h5 class="register">Sign Up</h5>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'Header',
        computed:{
            count(){
                return this.$store.state.cartItemCount
            }
        },
        methods:{
            goTo(page){
                page==='home'?this.$router.push("/"):this.$router.push("/cart")
            }
        }
    }
</script>

<style scoped>
    .header{
        height: 60px;
        background: lightgrey;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    .sweet{
        color:deeppink;
        font-weight: 600;
        margin-top: 7px;
    }
    .logo {
        display: flex;
        margin-left: 15px;
        align-items: center;
        padding: 6px;
        cursor: pointer;
    }
    .cart-count{
        font-size: 15px;
        color:#d4570cf2;
        padding: 0 5px;
        margin-left: -35px;
        background: #fff;
        height: 20px;
        border-radius: 50%;
        width: 20px;
    }
    .cart{
        width: 300px;
        justify-content: space-evenly;
        padding-top: 3px;
        align-items: center;
    }

    .login,.register{
        font-weight: 600;
        margin-top: 10px;
    }
    .cart-icon{
        cursor: pointer;
    }
</style>