<template>
  <div class="wrapper">
    <Header />
    <div class="content">
      <div class="container mb-5">
        <div class="row">
          <h4 class="my-4 fw-600 d-blue">Top rated</h4>
          <div
            @click="foodDetails(item)"
            v-for="item in topRated"
            :key="item.id"
            class="col-md-4 pointer"
          >
            <img
              :src="item.url"
              style="width:300px;height:300px;border-radius:15px;"
            />
            <h5 class="fw-600 black">{{ item.name }}</h5>
          </div>
        </div>

        <div class="row">
          <h4 class="my-4 fw-600 d-blue">All Categories</h4>
          <div
            @click="foodDetails(item)"
            v-for="item in allCategories"
            :key="item.id"
            class="col-md-4 pointer"
          >
            <img
              :src="item.url"
              style="width:300px;height:300px;border-radius:15px;"
            />
            <h5 class="fw-600 black">{{ item.name }}</h5>
          </div>
        </div>

        <div class="row">
          <h4 class="my-4 fw-600 d-blue">Top rated</h4>
          <div
            @click="foodDetails(item)"
            v-for="item in dishesNearYou"
            :key="item.id"
            class="col-md-4 pointer"
          >
            <img
              :src="item.url"
              style="width:300px;height:300px;border-radius:15px;"
            />
            <h5 class="fw-600 black">{{ item.name }}</h5>
          </div>
        </div>
      </div>
    </div>
    <Footer />
  </div>
</template>

<script>
import Header from "./common/Header.vue";
import Footer from "./common/Footer.vue";
export default {
  name: "Home",
  components: {
    Header,
    Footer,
  },
  computed: {
    topRated() {
      return this.$store.state.topRated;
    },
    allCategories() {
      return this.$store.state.allCategories;
    },
    dishesNearYou() {
      return this.$store.state.dishesNearYou;
    },
  },
  methods: {
    foodDetails(item) {
      this.$router.push({ name: "food-details", params: item });
    },
  },
};
</script>

<style scoped>
.wrapper {
  display: flex;
  flex-direction: column;
  height: 100vh;
}
.content {
  background: #efe9e2;
  flex: 1;
  overflow-y: auto;
}
.fw-600 {
  font-weight: 600;
}
.d-blue {
  color: dodgerblue;
}
.black {
  color: black;
}
.pointer {
  cursor: pointer;
}
</style>
